import React, { useState, useEffect, useContext } from "react";
import { MDBContainer, MDBRow, MDBBtn } from "mdb-react-ui-kit";

import Footer from "../components/footer/Footer";
import Header2 from "../components/header/Header2";
import Headermobile from "../components/header-mobile/Headermobile";
import Cardscommets from "../components/comments/Cardscommets";
import RateIcon2 from "../components/rate-icon/RateIcon2";
import AlertSuccess from "../components/Dashbord/AlertSuccess/AlertSuccess";
import { validate } from "../components/login/validate";
import axios from "axios";

import { DomainContext, CookieContext } from "../App";

const BusinessWriteComment = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);
  // cookie context
  const { cookieState, setCookieState } = useContext(CookieContext);

  // data
  const [data, setData] = useState({
    onvan: "",
    nazar: "",
    ReviewId: 0,
    Rate: 0,
  });

  const [com, setCom] = useState([]);
  const [bizTitle, setBizTitle] = useState("");

  const [disable, setDisable] = useState(false);
  const [apiError, setApiError] = useState(false);

  // validate
  const [errors, setErrors] = useState({});
  const [touched, setTouched] = useState({});

  const [showAlert, setshowAlert] = useState(false);
  const alertHandeler = () => {
    setshowAlert(true);
    setTimeout(() => {
      // After 3 seconds set the show value to false
      setshowAlert(false);
    }, 4000);
  };

  const inputChangeHandler = (event) => {
    setData({ ...data, [event.target.name]: event.target.value });
  };

  useEffect(() => {
    setErrors(validate(data, "writeComment"));
  }, [data, touched]);

  const submitHandler = (event) => {
    event.preventDefault();
    setApiError(false);
    console.log(data);

    if (!Object.keys(errors).length) {
      setDisable(true);
      // success
      const dataPost = {
        ReviewId: data.ReviewId,
        BizId: parseInt(props.match.params.id),
        MemId: getCookie("MemId"),
        Rate: data.Rate,
        ReviewTitle: data.onvan,
        ReviewBody: data.nazar,
      };
      console.log(dataPost);
      axios
        .post(domain + "/api/member/submit-review", dataPost, {
          headers: { ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9" },
        })
        .then((response) => {
          setDisable(false);
          console.log(response.data);
          if (response.data.msg == "done") {
            alertHandeler();
          } else if (response.data.msg == "update-error") {
            setApiError(true);
          }
        })
        .catch((error) => {
          // alert(error);
        });
    } else {
      setTouched({
        onvan: true,
        nazar: true,
      });
    }
  };

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    BizId: parseInt(props.match.params.id),
    MemId: getCookie("MemId"),
  };

  useEffect(() => {
    // axios
    //   .get(domain + "/api/latest-reviews", { headers: header })
    //   .then((response) => {
    //     console.log(response.data);
    //     setCom(response.data);
    //   })
    //   .catch((error) => {
    //     // alert(error);
    //   });
    axios
      .get(domain + "/api/get-member-review", { headers: header })
      .then((response) => {
        console.log(response.data);
        if (response.data.msg == "done") {
          // setCom(response.data.Review);
          if (
            response.data.Review.ReviewId != 0 ||
            response.data.Review.ReviewId != null
          ) {
            var info = response.data.Review;
            var onvan = info.CommentTitle == null ? "" : info.CommentTitle;
            var nazar = info.CommentBody == null ? "" : info.CommentBody;
            setData({
              onvan: onvan,
              nazar: nazar,
              ReviewId: info.ReviewId,
              Rate: info.Rate,
            });
          }
          setBizTitle(response.data.BizTitle);
        }
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  useEffect(() => {
    console.log("out");
    if (cookieState == "") {
      props.history.push({
        pathname: `/login?`,
        pathname: `/biz/${props.match.params.id}`,
      });
      // props.history.push({
      //   pathname: `/login?`,
      //   // pathname: `/login?target=/write-review/${props.match.params.id}`,
      //   // state: `/write-review/${props.match.params.id}`, // your data array of objects
      // });
    }
  });
  // useEffect(() => {
  //   console.log("out");
  //   if (cookieState == "") {
  //     // props.history.push({
  //     //   pathname: `/login?`,
  //     //   // pathname: `/login?target=/write-review/${props.match.params.id}`,
  //     //   // state: `/write-review/${props.match.params.id}`, // your data array of objects
  //     // });
  //   }
  // }, []);

  return (
    <>
      <div className="d-md-block d-none">
        {" "}
        <Header2 />{" "}
      </div>
      <div className="d-md-none d-block">
        <div className="fixed-header">
          <Headermobile />
        </div>
        <div className="box-under-header-res"></div>
      </div>
      <MDBContainer>
        <MDBRow>
          <div className="col-12 col-md-8  ">
            <div className="writecomment-div">
              <h4 className="writecomment-title1">
                <strong> </strong>   {bizTitle}{" "}
              </h4>
              <hr className="custom-hr2" />
              <div className="writecomment-rate">
                <h5 className="writecomment-text1"> </h5>
                <RateIcon2 data={data} setData={setData} />
              </div>

              <p className="writecomment-text2">    !</p>
              <label
                htmlFor="writecomment-title"
                className="writecomment-input1-label"
              >
                 
              </label>

              <input
                className={
                  errors.onvan && touched.onvan
                    ? "writecomment-input1 rb-error"
                    : "writecomment-input1 "
                }
                type="text"
                // name="writecomment-title"
                // className="writecomment-input1"
                name="onvan"
                value={data.onvan}
                onChange={inputChangeHandler}
              />
              <div className="position-relative">
                {errors.onvan && touched.onvan && (
                  <span className="input-error input-error-con me-1 input-error-wc">
                    {errors.onvan}
                  </span>
                )}
              </div>

              <label
                htmlFor="writecomment-text"
                className="writecomment-input1-label"
              >
                 
              </label>

              <textarea
                className={
                  errors.nazar && touched.nazar
                    ? "writecomment-input2 rb-error"
                    : "writecomment-input2 "
                }
                type="text"
                name="nazar"
                value={data.nazar}
                onChange={inputChangeHandler}
                onBlur={() => {
                  console.log("here");
                }}
                id=""
                placeholder="  ..."
              ></textarea>
              <div className="position-relative">
                {errors.nazar && touched.nazar && (
                  <span className="input-error input-error-con me-1 input-error-wc2">
                    {errors.nazar}
                  </span>
                )}
                {apiError && (
                  <span className="input-error input-error-con me-1 input-error-wc2">
                       .
                  </span>
                )}
              </div>

              <div className="centerr">
                <MDBBtn
                  className="writecomment-sabt hov-shadow-btn"
                  disabled={disable}
                  onClick={submitHandler}
                >
                   {" "}
                </MDBBtn>
              </div>
            </div>
          </div>
          <div className="col-md-4 d-md-block d-none ps-1">
            {/* <div className="writecomment-div2">
              <h4 className="writecomment-title2">
                    
              </h4>
              {com.map((item) => (
                <Cardscommets
                  key={item.Fullname}
                  rate={item.Rate}
                  likes={item.Agree}
                  name={item.Fullname}
                  date={item.CommentDate}
                  length={267}
                  text={item.CommentBody}
                  img={item.PhotoPath}
                />
              ))}
              <Cardscommets
                likes={5}
                length={217}
                text="It's a nice, sturdy little stand for a headset. The metal is actually stronger than I expected it to be, and there's rubber where it sits on the desk and where the headband of the headset rests on the stand, which is nice and keeps things where they're supposed to"
              />
            </div> */}
          </div>
        </MDBRow>
      </MDBContainer>
      <Footer />
      <AlertSuccess
        showAlert={showAlert}
        text={"      ."}
      />
    </>
  );
};

export default BusinessWriteComment;
