import React ,{useState} from "react";
import { MDBBtn } from "mdb-react-ui-kit";

import AlertSuccess from "../Dashbord/AlertSuccess/AlertSuccess";

import google from "../../images/home-body/google-play2.png";
import apple from "../../images/home-body/apple store2.png";

const Application = () => {

  const [showAlert, setshowAlert] = useState(false);
  const alertHandeler = () => {
    setshowAlert(true);
    setTimeout(() => {
      // After 3 seconds set the show value to false
      setshowAlert(false);
    }, 4000);
  };
  return (
    <div className="app-center app-btn-center">
      <div className="my-33 mb-xs-16 text-center app-text color-434343">
        <span className="fs-20 fw-bold fs-xs-14">  </span>
        {/* <br /> */}
        <p className="fs-18 fs-xs-14 mb-0 pt-md-4 mt-2 line-height19">
                     .
                {" "}
          <br className="d-none d-lg-block" />      
                      .
        </p>
      </div>
      <input
        type="email"
        className="app-input"
        placeholder="    "
      />
      <br />

      <MDBBtn onClick={alertHandeler} className="mt-27 app-btn hov-shadow-btn shadow-off my-xs-3 mb-60">
        
      </MDBBtn>

      <div className="d-flex text-center justify-content-center">
        <a href="">
          <img
            className="app-img ms-2 hov-shadow2 hov-trans"
            src={google}
            alt="google play"
          />
        </a>
        <a href="">
          <img
            className="app-img me-2 hov-shadow2 hov-trans"
            src={apple}
            alt="apple store"
          />
        </a>
      </div>
      {/* alert delete */}
      <AlertSuccess
        showAlert={showAlert}
        text={"      ."}
      />
    </div>
    
  );
};

export default Application;
